<template>
	<div @touchstart="touchstart" @touchmove="touchmove" @touchend="touchend" ref="wrap" class="wrap">
	
			<slot ></slot>
	



	</div>
</template>

<script>
export default{ 
	name:"scroll",
	computed:{
		maxScrollTop(){
return this.pH-this.wH
		}
	},
	data(){
		return{
			wH:0,
			pH:0,
			touchStart:0,
			change:0,
			curScroll:0,

		}
	},
	mounted(){
		this.wH=window.innerHeight;
		this.pH=this.$refs.wrap.offsetHeight
		// console.log(this.wH,this.pH)
	},
	methods:{

		touchstart(e){
			this.pH=this.$refs.wrap.offsetHeight;
			this.wH=window.innerHeight;
			// console.log(this.pH)
			this.$refs.wrap.style.transition='';
			this.touchStart=e.touches[0].clientY;
			this.curScroll=document.documentElement.scrollTop||document.body.scrollTop;


		},
		touchmove(e){
			let change=e.touches[0].clientY-this.touchStart;
			// console.log(this.curScroll,this.maxScrollTop,this.$refs.wrap.clientHeight-window.innerHeight)

			if(this.curScroll<=0&&change>0){
				change=Math.min(change,200);
				this.$refs.wrap.style.transform=`translateY(${change}px)`;


			}else if(this.curScroll>=this.maxScrollTop&&change<0){
				change=Math.max(change,-200);
				this.$refs.wrap.style.transform=`translateY(${change}px)`;

			}else{
				// console.log(this.curScroll,change)
				window.scrollTo(0,`${this.curScroll-change}`)
			}
this.change=change;

		},
		touchend(){
			// console.log(this.curScroll)
		if(this.curScroll<=0||this.curScroll>=this.maxScrollTop){
			this.$refs.wrap.style.transition="all .2s linear";

			this.$refs.wrap.style.transform="translateY(0)";

		}
		if(this.curScroll>this.maxScrollTop){
			this.endEvent();
		}
			
			this.change=0;
		},
		endEvent(){
			
			this.$emit("end");
		}
		

	}



}

</script>

<style lang="less" type="text/less" scoped>
</style>


